<style lang="sass">
    @import '../assets/scss/iconfont/iconfont.css';
    @import '../assets/scss/CV.scss';
    @import '../assets/scss/github-markdown.css';
    .index{
        width: 100%;
        background-color: #fff;
        margin-top: 40%;
    }
    .hide{
        display:none !important;
    }
</style>




